.product-display{
    display: flex;
    margin: 0px 170px;
}
.product-display-left{
    display: flex;
    gap: 17px;
}
.product-display-img-list{
    display: flex;
    flex-direction: column;
    gap: 16px;
}
.product-display-img-list img{
    height: 163px;
}
.product-display-main-img{
    width: 586px;
    height: 700px;
}
.product-display-right{
    display: flex;
    flex-direction: column;
    margin: 0px 70px;
}
.product-display-right h1{
    color: #5e5e5e;
    font-size: 40px;
    font-weight: 700;
}
.product-display-right-star{
    display: flex;
    align-items: center;
    margin-top: 13px;
    gap: 5px;
    color: #1c1c1c;
    font-size: 16px;
}
.product-display-right-prices{
    display: flex;
    gap: 30px;
    margin: 40px 0px;
    font-size: 24px;
    font-weight: 700;
}
.product-display-right-price-old{
    color: #5e5e5e;
    text-decoration: line-through;
}
.product-display-right-size h1{
    margin-top: 55px;
    color: #5e5e5e;
    font-size: 20px;
    font-weight: 600;
}
.product-display-right-sizes{
    display: flex;
    margin: 30px 0px;
    gap: 20px;
}
.product-display-right-sizes div{
    padding: 18px 24px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
    border-radius: 3px;
    cursor: pointer;
}
.add-to-cart{
    padding: 20px 40px;
    font-size: 16px;
    font-weight: 600;
    color: white;
    background-color: #ff4141;
    border: none;
    outline: none;
    width: 200px;
}
